body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  text-align: center;
  text-transform: uppercase;
  justify-content: space-between;
  max-width: 1200px;
  height: 60px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

.header__img {
  margin: auto 0;
}

.header__nav {
  text-align: center;
  justify-content: space-between;
  padding: 0;
  display: flex;
}

.header__list {
  text-align: left;
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
  list-style-type: none;
}

.header__list:not(:last-child) {
  padding-right: 40px;
}

.header__link {
  color: #000;
  text-decoration: none;
  position: relative;
}

.header__link:hover {
  color: #00acdc;
}

.header__ink:visited {
  color: #000;
}

.header__link:before {
  content: "";
  visibility: hidden;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 2px;
  transition: transform .3s;
  position: absolute;
  bottom: -20px;
  transform: scaleX(0);
}

.header__link:hover:before {
  visibility: visible;
  transform: scaleX(1);
}

@media screen and (width <= 1024px) {
  .header {
    max-width: 1024px;
  }

  .header__list:not(:last-child) {
    padding-right: 30px;
  }
}
